body {
  display: flex;
  justify-content: center;
}

.main-wrapper {
  position: relative;
  width: 8 * $gutter-multiplier * $c;
  height: 100vh;
  background-color: $grey;
  padding: 0 1.5 * $c;
}

.buttons-sliders-wrapper {
  display: flex;
  gap: $half-c;
  margin: $c 0 0 0;
}

.inner-wrapper {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100%;
}

.buttons-wrapper {
  align-self: flex-start;
  display: grid;
  gap: $fourth-c;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  flex-grow: none;
  width: 3 * $gutter-multiplier * $c;
}

.sliders-wrapper {
  display: flex;
  gap: $fourth-c;
}

.strips-wrapper {
  display: flex;
  height: 100vh;
  width: 100px;
  position: absolute;
  left: 0;
  top: 0;
}

.keyboard-wrapper {
  margin: $c 0 0 0;
  position: relative;
}

.white-keys-wrapper {
  display: flex;
}

.black-keys-wrapper {
  display: flex;
  gap: 0.45 * $c;
  position: absolute;
  top: 0;
  left: 0.45 * $c + 1;
}

@media only screen and (max-width: 768px) {
  .buttons-sliders-wrapper {
    display: flex;
    flex-direction: column;
  }
  .main-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100%;
    padding: 0;
  }
  .inner-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
  }
  .keyboard-wrapper {
    display: none;
  }
  .sliders-wrapper {
    gap: 0;
    margin: $c 0 $c 0;
    display: flex;
    justify-content: center;
  }
  .strips-wrapper {
    display: none;
  }

  .buttons-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100vw;
    gap: $half-c;
  }
}
