.slider-wrapper {
  position: relative;
  display: flex;
  gap: $c;
  align-items: center;
  width: 220px;
  height: 220px;
  flex-direction: column;
}

.slider {
  appearance: none;
  width: 220px;
  height: 9px;
  transform: rotate(270deg) translateX(-50%);
  background-color: $black;
}

.slider::-moz-range-thumb {
  border: 0;
  border-radius: 0;
  height: $half-c;
  width: $fourth-c;
  &:hover {
    cursor: pointer;
  }
}

.slider::-webkit-slider-thumb {
  appearance: none;
  border: 0;
  border-radius: 0;
  height: $half-c;
  width: $fourth-c;
  &:hover {
    cursor: pointer;
  }
}

.slider#red::-webkit-slider-thumb {
  background-color: $red;
}

.slider#red::-moz-range-thumb {
  background-color: $red;
}

.slider#yellow::-webkit-slider-thumb {
  background-color: $yellow;
}

.slider#yellow::-moz-range-thumb {
  background-color: $yellow;
}

.slider__label {
  position: absolute;
  text-align: center;
}

.slider__label--top {
  top: -$half-c;
}

.slider__label--bottom {
  bottom: -$half-c;
}

.slider__label--long {
  transform: translateY(-50%);
}

@media screen and (max-width: 768px) {
  .slider-wrapper {
    width: 25vw;
  }
}
