.white-key {
  background-color: $light-grey;
  height: 2.5 * $c;
  width: 0.85 * $c;
  outline: solid 3px $black;
  z-index: 1;
  &:hover {
    cursor: pointer;
    background-color: $white-hover;
  }
}

.black-key {
  z-index: 2;
  background-color: $black;
  height: 1.75 * $c;
  width: 0.4 * $c;
  &:hover {
    cursor: pointer;
    background-color: $black-hover;
  }
  &.hidden {
    opacity: 0;
    z-index: -1;
    cursor: default;
  }
}
