@use 'sass:math';
@import url('https://fonts.googleapis.com/css2?family=Inspiration&family=VT323&display=swap');

//COLORS
$red: #d04648;
$blue: #597dce;
$yellow: #dad45e;
$yellow-hover: #eae588;
$black: #140c1c;
$black-hover: #100a18;
$grey: #442434;
$white: #deeed6;
$white-hover: #c6cbc4;
$light-grey: #d9d9d9;

//SPACING
$c: 1rem * math.div(138, 16);
$half-c: math.div($c, 2);
$fourth-c: math.div($c, 4);
$eighth-c: math.div($c, 8);
$gutter-multiplier: math.div(158, 138);

//FONTS
$main-font: 'VT323', monospace;
$sub-font: 'Inspiration', cursive;
